"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Badge } from "@/components/ui/badge";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion";
import {
  AlertCircle,
  Server,
  Plus,
  Edit,
  Trash2,
  Play,
  Pause,
  Code,
  Webhook,
  Terminal,
  Database,
  Shield,
  Zap,
} from "lucide-react";

interface TaskAction {
  id: string;
  type: "command" | "api_call" | "notification" | "script";
  name: string;
  config: {
    command?: string;
    script?: string;
    apiUrl?: string;
    method?: string;
    headers?: Record<string, string>;
    body?: string;
    notificationMessage?: string;
  };
}

interface TaskTrigger {
  type: "webhook" | "schedule" | "api_monitor" | "log_monitor" | "custom";
  config: {
    webhookUrl?: string;
    schedule?: string;
    apiUrl?: string;
    logPath?: string;
    condition?: string;
    customTrigger?: string;
  };
}

interface AdvancedTask {
  id: string;
  name: string;
  description: string;
  category: "server" | "database" | "security" | "monitoring" | "custom";
  prompt: string;
  trigger: TaskTrigger;
  conditions: {
    field: string;
    operator: "equals" | "contains" | "greater_than" | "less_than" | "regex";
    value: string;
  }[];
  actions: TaskAction[];
  variables: Record<string, string>;
  status: "active" | "inactive" | "error";
  lastRun?: string;
  runCount: number;
}

const taskCategories = [
  {
    value: "server",
    label: "Administración de Servidor",
    icon: Server,
    color: "bg-blue-500",
  },
  {
    value: "database",
    label: "Base de Datos",
    icon: Database,
    color: "bg-green-500",
  },
  { value: "security", label: "Seguridad", icon: Shield, color: "bg-red-500" },
  {
    value: "monitoring",
    label: "Monitoreo",
    icon: AlertCircle,
    color: "bg-yellow-500",
  },
  {
    value: "custom",
    label: "Personalizada",
    icon: Zap,
    color: "bg-purple-500",
  },
];

const triggerTypes = [
  {
    value: "webhook",
    label: "Webhook",
    description: "Recibe alertas via HTTP",
  },
  {
    value: "schedule",
    label: "Programado",
    description: "Ejecuta en horarios específicos",
  },
  {
    value: "api_monitor",
    label: "Monitor API",
    description: "Monitorea endpoints",
  },
  {
    value: "log_monitor",
    label: "Monitor de Logs",
    description: "Analiza archivos de log",
  },
  {
    value: "custom",
    label: "Personalizado",
    description: "Trigger personalizado",
  },
];

const actionTypes = [
  {
    value: "command",
    label: "Comando del Sistema",
    description: "Ejecuta comandos bash/shell",
  },
  {
    value: "script",
    label: "Script Personalizado",
    description: "Ejecuta código personalizado",
  },
  {
    value: "api_call",
    label: "Llamada API",
    description: "Realiza peticiones HTTP",
  },
  {
    value: "notification",
    label: "Notificación",
    description: "Envía alertas/mensajes",
  },
];

export default function AdvancedBotTasks() {
  const [tasks, setTasks] = useState<AdvancedTask[]>([
    {
      id: "1",
      name: "Reinicio Automático de Docker",
      description:
        "Detecta cuando un contenedor Docker se cae y lo reinicia automáticamente",
      category: "server",
      prompt:
        "Cuando recibas una alerta de que un contenedor Docker se ha caído, analiza la información y ejecuta los comandos necesarios para reiniciarlo. Si el problema persiste, notifica al administrador con detalles específicos.",
      trigger: {
        type: "webhook",
        config: {
          webhookUrl: "https://mi-bot.com/webhook/docker-alert",
          condition: "container_status == 'down'",
        },
      },
      conditions: [
        { field: "container_status", operator: "equals", value: "down" },
        { field: "service_name", operator: "contains", value: "web" },
      ],
      actions: [
        {
          id: "1",
          type: "command",
          name: "Reiniciar Contenedor",
          config: {
            command: "docker restart {{container_name}}",
          },
        },
        {
          id: "2",
          type: "command",
          name: "Verificar Estado",
          config: {
            command: "docker ps | grep {{container_name}}",
          },
        },
        {
          id: "3",
          type: "notification",
          name: "Notificar Administrador",
          config: {
            notificationMessage:
              "Contenedor {{container_name}} reiniciado automáticamente. Estado: {{status}}",
          },
        },
      ],
      variables: {
        container_name: "web-app",
        max_retries: "3",
        timeout: "30",
      },
      status: "active",
      lastRun: "2024-01-15 14:30:00",
      runCount: 15,
    },
  ]);

  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [editingTask, setEditingTask] = useState<AdvancedTask | null>(null);
  const [newTask, setNewTask] = useState<Partial<AdvancedTask>>({
    name: "",
    description: "",
    category: "server",
    prompt: "",
    trigger: { type: "webhook", config: {} },
    conditions: [],
    actions: [],
    variables: {},
    status: "inactive",
    runCount: 0,
  });

  const handleSaveTask = () => {
    if (editingTask) {
      setTasks(
        tasks.map((task) =>
          task.id === editingTask.id
            ? ({ ...editingTask, ...newTask } as AdvancedTask)
            : task
        )
      );
    } else {
      const task: AdvancedTask = {
        id: Date.now().toString(),
        name: newTask.name || "",
        description: newTask.description || "",
        category: newTask.category || "server",
        prompt: newTask.prompt || "",
        trigger: newTask.trigger || { type: "webhook", config: {} },
        conditions: newTask.conditions || [],
        actions: newTask.actions || [],
        variables: newTask.variables || {},
        status: "inactive",
        runCount: 0,
      };
      setTasks([...tasks, task]);
    }

    setIsDialogOpen(false);
    setEditingTask(null);
    resetNewTask();
  };

  const resetNewTask = () => {
    setNewTask({
      name: "",
      description: "",
      category: "server",
      prompt: "",
      trigger: { type: "webhook", config: {} },
      conditions: [],
      actions: [],
      variables: {},
      status: "inactive",
      runCount: 0,
    });
  };

  const addCondition = () => {
    setNewTask({
      ...newTask,
      conditions: [
        ...(newTask.conditions || []),
        { field: "", operator: "equals", value: "" },
      ],
    });
  };

  const updateCondition = (
    index: number,
    field: keyof (typeof newTask.conditions)[0],
    value: string
  ) => {
    const conditions = [...(newTask.conditions || [])];
    conditions[index] = { ...conditions[index], [field]: value };
    setNewTask({ ...newTask, conditions });
  };

  const removeCondition = (index: number) => {
    const conditions = [...(newTask.conditions || [])];
    conditions.splice(index, 1);
    setNewTask({ ...newTask, conditions });
  };

  const addAction = () => {
    const newAction: TaskAction = {
      id: Date.now().toString(),
      type: "command",
      name: "",
      config: {},
    };
    setNewTask({
      ...newTask,
      actions: [...(newTask.actions || []), newAction],
    });
  };

  const updateAction = (index: number, field: keyof TaskAction, value: any) => {
    const actions = [...(newTask.actions || [])];
    if (field === "config") {
      actions[index] = {
        ...actions[index],
        config: { ...actions[index].config, ...value },
      };
    } else {
      actions[index] = { ...actions[index], [field]: value };
    }
    setNewTask({ ...newTask, actions });
  };

  const removeAction = (index: number) => {
    const actions = [...(newTask.actions || [])];
    actions.splice(index, 1);
    setNewTask({ ...newTask, actions });
  };

  const addVariable = (key: string, value: string) => {
    setNewTask({
      ...newTask,
      variables: { ...(newTask.variables || {}), [key]: value },
    });
  };

  const removeVariable = (key: string) => {
    const variables = { ...(newTask.variables || {}) };
    delete variables[key];
    setNewTask({ ...newTask, variables });
  };

  const toggleTaskStatus = (taskId: string) => {
    setTasks(
      tasks.map((task) =>
        task.id === taskId
          ? {
              ...task,
              status: task.status === "active" ? "inactive" : "active",
            }
          : task
      )
    );
  };

  const getCategoryInfo = (category: string) => {
    return (
      taskCategories.find((c) => c.value === category) || taskCategories[0]
    );
  };

  return (
    <div className="p-6 max-w-7xl mx-auto">
      <div className="flex justify-between items-center mb-6">
        <div>
          <h1 className="text-3xl font-bold">Tareas Automatizadas Avanzadas</h1>
          <p className="text-muted-foreground mt-2">
            Configura tareas complejas con triggers, condiciones y acciones
            personalizadas
          </p>
        </div>

        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button onClick={resetNewTask}>
              <Plus className="w-4 h-4 mr-2" />
              Nueva Tarea Avanzada
            </Button>
          </DialogTrigger>

          <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
            <DialogHeader>
              <DialogTitle>
                {editingTask ? "Editar Tarea" : "Nueva Tarea Automatizada"}
              </DialogTitle>
              <DialogDescription>
                Configura una tarea avanzada con triggers, condiciones y
                acciones personalizadas
              </DialogDescription>
            </DialogHeader>

            <Tabs defaultValue="basic" className="w-full">
              <TabsList className="grid w-full grid-cols-5">
                <TabsTrigger value="basic">Básico</TabsTrigger>
                <TabsTrigger value="trigger">Trigger</TabsTrigger>
                <TabsTrigger value="conditions">Condiciones</TabsTrigger>
                <TabsTrigger value="actions">Acciones</TabsTrigger>
                <TabsTrigger value="variables">Variables</TabsTrigger>
              </TabsList>

              <TabsContent value="basic" className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="task-name">Nombre de la Tarea</Label>
                    <Input
                      id="task-name"
                      placeholder="Ej: Reinicio automático de Docker"
                      value={newTask.name}
                      onChange={(e) =>
                        setNewTask({ ...newTask, name: e.target.value })
                      }
                    />
                  </div>
                  <div>
                    <Label htmlFor="task-category">Categoría</Label>
                    <Select
                      value={newTask.category}
                      onValueChange={(value) =>
                        setNewTask({
                          ...newTask,
                          category: value as AdvancedTask["category"],
                        })
                      }
                    >
                      <SelectTrigger>
                        <SelectValue />
                      </SelectTrigger>
                      <SelectContent>
                        {taskCategories.map((category) => {
                          const Icon = category.icon;
                          return (
                            <SelectItem
                              key={category.value}
                              value={category.value}
                            >
                              <div className="flex items-center">
                                <Icon className="w-4 h-4 mr-2" />
                                {category.label}
                              </div>
                            </SelectItem>
                          );
                        })}
                      </SelectContent>
                    </Select>
                  </div>
                </div>

                <div>
                  <Label htmlFor="task-description">Descripción</Label>
                  <Input
                    id="task-description"
                    placeholder="Breve descripción de lo que hace esta tarea"
                    value={newTask.description}
                    onChange={(e) =>
                      setNewTask({ ...newTask, description: e.target.value })
                    }
                  />
                </div>

                <div>
                  <Label htmlFor="task-prompt">Contexto/Prompt del Bot</Label>
                  <Textarea
                    id="task-prompt"
                    placeholder="Describe cómo debe comportarse el bot cuando se active esta tarea..."
                    value={newTask.prompt}
                    onChange={(e) =>
                      setNewTask({ ...newTask, prompt: e.target.value })
                    }
                    rows={6}
                  />
                </div>
              </TabsContent>

              <TabsContent value="trigger" className="space-y-4">
                <div>
                  <Label>Tipo de Trigger</Label>
                  <Select
                    value={newTask.trigger?.type}
                    onValueChange={(value) =>
                      setNewTask({
                        ...newTask,
                        trigger: {
                          type: value as TaskTrigger["type"],
                          config: {},
                        },
                      })
                    }
                  >
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      {triggerTypes.map((trigger) => (
                        <SelectItem key={trigger.value} value={trigger.value}>
                          <div>
                            <div className="font-medium">{trigger.label}</div>
                            <div className="text-xs text-muted-foreground">
                              {trigger.description}
                            </div>
                          </div>
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                {newTask.trigger?.type === "webhook" && (
                  <div>
                    <Label htmlFor="webhook-url">URL del Webhook</Label>
                    <Input
                      id="webhook-url"
                      placeholder="https://mi-bot.com/webhook/alert"
                      value={newTask.trigger.config.webhookUrl || ""}
                      onChange={(e) =>
                        setNewTask({
                          ...newTask,
                          trigger: {
                            ...newTask.trigger,
                            config: {
                              ...newTask.trigger.config,
                              webhookUrl: e.target.value,
                            },
                          },
                        })
                      }
                    />
                  </div>
                )}

                {newTask.trigger?.type === "api_monitor" && (
                  <div>
                    <Label htmlFor="api-url">URL de la API a Monitorear</Label>
                    <Input
                      id="api-url"
                      placeholder="https://mi-servidor.com/health"
                      value={newTask.trigger.config.apiUrl || ""}
                      onChange={(e) =>
                        setNewTask({
                          ...newTask,
                          trigger: {
                            ...newTask.trigger,
                            config: {
                              ...newTask.trigger.config,
                              apiUrl: e.target.value,
                            },
                          },
                        })
                      }
                    />
                  </div>
                )}

                {newTask.trigger?.type === "schedule" && (
                  <div>
                    <Label htmlFor="schedule">Programación (Cron)</Label>
                    <Input
                      id="schedule"
                      placeholder="0 */5 * * * (cada 5 minutos)"
                      value={newTask.trigger.config.schedule || ""}
                      onChange={(e) =>
                        setNewTask({
                          ...newTask,
                          trigger: {
                            ...newTask.trigger,
                            config: {
                              ...newTask.trigger.config,
                              schedule: e.target.value,
                            },
                          },
                        })
                      }
                    />
                  </div>
                )}
              </TabsContent>

              <TabsContent value="conditions" className="space-y-4">
                <div className="flex justify-between items-center">
                  <Label>Condiciones para Activar la Tarea</Label>
                  <Button
                    type="button"
                    variant="outline"
                    size="sm"
                    onClick={addCondition}
                  >
                    <Plus className="w-4 h-4 mr-2" />
                    Agregar Condición
                  </Button>
                </div>

                {newTask.conditions?.map((condition, index) => (
                  <Card key={index}>
                    <CardContent className="pt-4">
                      <div className="grid grid-cols-4 gap-2 items-end">
                        <div>
                          <Label>Campo</Label>
                          <Input
                            placeholder="ej: container_status"
                            value={condition.field}
                            onChange={(e) =>
                              updateCondition(index, "field", e.target.value)
                            }
                          />
                        </div>
                        <div>
                          <Label>Operador</Label>
                          <Select
                            value={condition.operator}
                            onValueChange={(value) =>
                              updateCondition(index, "operator", value)
                            }
                          >
                            <SelectTrigger>
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              <SelectItem value="equals">Igual a</SelectItem>
                              <SelectItem value="contains">Contiene</SelectItem>
                              <SelectItem value="greater_than">
                                Mayor que
                              </SelectItem>
                              <SelectItem value="less_than">
                                Menor que
                              </SelectItem>
                              <SelectItem value="regex">Regex</SelectItem>
                            </SelectContent>
                          </Select>
                        </div>
                        <div>
                          <Label>Valor</Label>
                          <Input
                            placeholder="ej: down"
                            value={condition.value}
                            onChange={(e) =>
                              updateCondition(index, "value", e.target.value)
                            }
                          />
                        </div>
                        <Button
                          type="button"
                          variant="outline"
                          size="sm"
                          onClick={() => removeCondition(index)}
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </TabsContent>

              <TabsContent value="actions" className="space-y-4">
                <div className="flex justify-between items-center">
                  <Label>Acciones a Ejecutar</Label>
                  <Button
                    type="button"
                    variant="outline"
                    size="sm"
                    onClick={addAction}
                  >
                    <Plus className="w-4 h-4 mr-2" />
                    Agregar Acción
                  </Button>
                </div>

                {newTask.actions?.map((action, index) => (
                  <Card key={action.id}>
                    <CardHeader className="pb-3">
                      <div className="flex justify-between items-center">
                        <CardTitle className="text-sm">
                          Acción {index + 1}
                        </CardTitle>
                        <Button
                          type="button"
                          variant="outline"
                          size="sm"
                          onClick={() => removeAction(index)}
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    </CardHeader>
                    <CardContent className="space-y-3">
                      <div className="grid grid-cols-2 gap-3">
                        <div>
                          <Label>Nombre de la Acción</Label>
                          <Input
                            placeholder="ej: Reiniciar contenedor"
                            value={action.name}
                            onChange={(e) =>
                              updateAction(index, "name", e.target.value)
                            }
                          />
                        </div>
                        <div>
                          <Label>Tipo de Acción</Label>
                          <Select
                            value={action.type}
                            onValueChange={(value) =>
                              updateAction(index, "type", value)
                            }
                          >
                            <SelectTrigger>
                              <SelectValue />
                            </SelectTrigger>
                            <SelectContent>
                              {actionTypes.map((type) => (
                                <SelectItem key={type.value} value={type.value}>
                                  <div>
                                    <div className="font-medium">
                                      {type.label}
                                    </div>
                                    <div className="text-xs text-muted-foreground">
                                      {type.description}
                                    </div>
                                  </div>
                                </SelectItem>
                              ))}
                            </SelectContent>
                          </Select>
                        </div>
                      </div>

                      {action.type === "command" && (
                        <div>
                          <Label>Comando a Ejecutar</Label>
                          <Textarea
                            placeholder="docker restart {{container_name}}"
                            value={action.config.command || ""}
                            onChange={(e) =>
                              updateAction(index, "config", {
                                command: e.target.value,
                              })
                            }
                            rows={2}
                          />
                        </div>
                      )}

                      {action.type === "script" && (
                        <div>
                          <Label>Script/Código</Label>
                          <Textarea
                            placeholder="#!/bin/bash&#10;echo 'Ejecutando script personalizado'&#10;# Tu código aquí"
                            value={action.config.script || ""}
                            onChange={(e) =>
                              updateAction(index, "config", {
                                script: e.target.value,
                              })
                            }
                            rows={4}
                          />
                        </div>
                      )}

                      {action.type === "api_call" && (
                        <div className="space-y-2">
                          <div className="grid grid-cols-2 gap-2">
                            <div>
                              <Label>URL de la API</Label>
                              <Input
                                placeholder="https://api.ejemplo.com/restart"
                                value={action.config.apiUrl || ""}
                                onChange={(e) =>
                                  updateAction(index, "config", {
                                    apiUrl: e.target.value,
                                  })
                                }
                              />
                            </div>
                            <div>
                              <Label>Método HTTP</Label>
                              <Select
                                value={action.config.method || "POST"}
                                onValueChange={(value) =>
                                  updateAction(index, "config", {
                                    method: value,
                                  })
                                }
                              >
                                <SelectTrigger>
                                  <SelectValue />
                                </SelectTrigger>
                                <SelectContent>
                                  <SelectItem value="GET">GET</SelectItem>
                                  <SelectItem value="POST">POST</SelectItem>
                                  <SelectItem value="PUT">PUT</SelectItem>
                                  <SelectItem value="DELETE">DELETE</SelectItem>
                                </SelectContent>
                              </Select>
                            </div>
                          </div>
                          <div>
                            <Label>Body (JSON)</Label>
                            <Textarea
                              placeholder='{"container": "{{container_name}}"}'
                              value={action.config.body || ""}
                              onChange={(e) =>
                                updateAction(index, "config", {
                                  body: e.target.value,
                                })
                              }
                              rows={2}
                            />
                          </div>
                        </div>
                      )}

                      {action.type === "notification" && (
                        <div>
                          <Label>Mensaje de Notificación</Label>
                          <Textarea
                            placeholder="Contenedor {{container_name}} reiniciado exitosamente"
                            value={action.config.notificationMessage || ""}
                            onChange={(e) =>
                              updateAction(index, "config", {
                                notificationMessage: e.target.value,
                              })
                            }
                            rows={2}
                          />
                        </div>
                      )}
                    </CardContent>
                  </Card>
                ))}
              </TabsContent>

              <TabsContent value="variables" className="space-y-4">
                <div>
                  <Label>Variables Personalizadas</Label>
                  <p className="text-sm text-muted-foreground mb-3">
                    Define variables que puedes usar en tus comandos y acciones
                    usando {`{{variable_name}}`}
                  </p>
                </div>

                <div className="space-y-2">
                  {Object.entries(newTask.variables || {}).map(
                    ([key, value]) => (
                      <div key={key} className="flex gap-2">
                        <Input value={key} readOnly className="flex-1" />
                        <Input
                          value={value}
                          onChange={(e) => addVariable(key, e.target.value)}
                          className="flex-1"
                        />
                        <Button
                          type="button"
                          variant="outline"
                          size="sm"
                          onClick={() => removeVariable(key)}
                        >
                          <Trash2 className="w-4 h-4" />
                        </Button>
                      </div>
                    )
                  )}
                </div>

                <div className="flex gap-2">
                  <Input placeholder="nombre_variable" id="new-var-key" />
                  <Input placeholder="valor" id="new-var-value" />
                  <Button
                    type="button"
                    onClick={() => {
                      const keyInput = document.getElementById(
                        "new-var-key"
                      ) as HTMLInputElement;
                      const valueInput = document.getElementById(
                        "new-var-value"
                      ) as HTMLInputElement;
                      if (keyInput.value && valueInput.value) {
                        addVariable(keyInput.value, valueInput.value);
                        keyInput.value = "";
                        valueInput.value = "";
                      }
                    }}
                  >
                    <Plus className="w-4 h-4" />
                  </Button>
                </div>
              </TabsContent>
            </Tabs>

            <DialogFooter>
              <Button variant="outline" onClick={() => setIsDialogOpen(false)}>
                Cancelar
              </Button>
              <Button onClick={handleSaveTask}>
                {editingTask ? "Actualizar" : "Crear"} Tarea
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid gap-6">
        {tasks.map((task) => {
          const categoryInfo = getCategoryInfo(task.category);
          const Icon = categoryInfo.icon;

          return (
            <Card key={task.id} className="relative">
              <CardHeader>
                <div className="flex items-start justify-between">
                  <div className="flex items-center space-x-3">
                    <div className={`p-2 rounded-lg ${categoryInfo.color}`}>
                      <Icon className="w-5 h-5 text-white" />
                    </div>
                    <div>
                      <CardTitle className="text-xl">{task.name}</CardTitle>
                      <CardDescription className="flex items-center space-x-2 mt-1">
                        <Badge variant="outline">{categoryInfo.label}</Badge>
                        <div
                          className={`w-2 h-2 rounded-full ${
                            task.status === "active"
                              ? "bg-green-500"
                              : task.status === "error"
                              ? "bg-red-500"
                              : "bg-gray-500"
                          }`}
                        />
                        <span className="text-xs capitalize">
                          {task.status}
                        </span>
                        <span className="text-xs">
                          • {task.runCount} ejecuciones
                        </span>
                      </CardDescription>
                    </div>
                  </div>

                  <div className="flex items-center space-x-2">
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => toggleTaskStatus(task.id)}
                    >
                      {task.status === "active" ? (
                        <Pause className="w-4 h-4" />
                      ) : (
                        <Play className="w-4 h-4" />
                      )}
                    </Button>
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => {
                        setEditingTask(task);
                        setNewTask(task);
                        setIsDialogOpen(true);
                      }}
                    >
                      <Edit className="w-4 h-4" />
                    </Button>
                    <Button
                      variant="ghost"
                      size="sm"
                      onClick={() => {
                        setTasks(tasks.filter((t) => t.id !== task.id));
                      }}
                    >
                      <Trash2 className="w-4 h-4" />
                    </Button>
                  </div>
                </div>
              </CardHeader>

              <CardContent>
                <div className="space-y-4">
                  <p className="text-sm text-muted-foreground">
                    {task.description}
                  </p>

                  <Accordion type="single" collapsible className="w-full">
                    <AccordionItem value="prompt">
                      <AccordionTrigger className="text-sm">
                        <div className="flex items-center">
                          <Code className="w-4 h-4 mr-2" />
                          Contexto del Bot
                        </div>
                      </AccordionTrigger>
                      <AccordionContent>
                        <div className="bg-muted p-3 rounded-md text-sm">
                          {task.prompt}
                        </div>
                      </AccordionContent>
                    </AccordionItem>

                    <AccordionItem value="trigger">
                      <AccordionTrigger className="text-sm">
                        <div className="flex items-center">
                          <Webhook className="w-4 h-4 mr-2" />
                          Trigger:{" "}
                          {
                            triggerTypes.find(
                              (t) => t.value === task.trigger.type
                            )?.label
                          }
                        </div>
                      </AccordionTrigger>
                      <AccordionContent>
                        <div className="space-y-2 text-sm">
                          {Object.entries(task.trigger.config).map(
                            ([key, value]) => (
                              <div key={key} className="flex justify-between">
                                <span className="font-medium">{key}:</span>
                                <span className="text-muted-foreground">
                                  {value}
                                </span>
                              </div>
                            )
                          )}
                        </div>
                      </AccordionContent>
                    </AccordionItem>

                    <AccordionItem value="actions">
                      <AccordionTrigger className="text-sm">
                        <div className="flex items-center">
                          <Terminal className="w-4 h-4 mr-2" />
                          Acciones ({task.actions.length})
                        </div>
                      </AccordionTrigger>
                      <AccordionContent>
                        <div className="space-y-3">
                          {task.actions.map((action, index) => (
                            <div
                              key={action.id}
                              className="border rounded-md p-3"
                            >
                              <div className="flex items-center justify-between mb-2">
                                <span className="font-medium text-sm">
                                  {action.name}
                                </span>
                                <Badge variant="secondary">
                                  {
                                    actionTypes.find(
                                      (t) => t.value === action.type
                                    )?.label
                                  }
                                </Badge>
                              </div>
                              {action.config.command && (
                                <code className="text-xs bg-muted p-2 rounded block">
                                  {action.config.command}
                                </code>
                              )}
                              {action.config.script && (
                                <code className="text-xs bg-muted p-2 rounded block whitespace-pre-wrap">
                                  {action.config.script}
                                </code>
                              )}
                            </div>
                          ))}
                        </div>
                      </AccordionContent>
                    </AccordionItem>

                    {Object.keys(task.variables).length > 0 && (
                      <AccordionItem value="variables">
                        <AccordionTrigger className="text-sm">
                          <div className="flex items-center">
                            <Zap className="w-4 h-4 mr-2" />
                            Variables ({Object.keys(task.variables).length})
                          </div>
                        </AccordionTrigger>
                        <AccordionContent>
                          <div className="grid grid-cols-2 gap-2 text-sm">
                            {Object.entries(task.variables).map(
                              ([key, value]) => (
                                <div
                                  key={key}
                                  className="flex justify-between p-2 bg-muted rounded"
                                >
                                  <span className="font-medium">{`{{${key}}}`}</span>
                                  <span className="text-muted-foreground">
                                    {value}
                                  </span>
                                </div>
                              )
                            )}
                          </div>
                        </AccordionContent>
                      </AccordionItem>
                    )}
                  </Accordion>

                  {task.lastRun && (
                    <div className="text-xs text-muted-foreground">
                      Última ejecución: {task.lastRun}
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          );
        })}

        {tasks.length === 0 && (
          <Card className="text-center py-12">
            <CardContent>
              <Server className="w-12 h-12 text-muted-foreground mx-auto mb-4" />
              <h3 className="text-lg font-medium mb-2">
                No hay tareas configuradas
              </h3>
              <p className="text-muted-foreground mb-4">
                Crea tu primera tarea automatizada avanzada con triggers y
                acciones personalizadas
              </p>
              <Button onClick={() => setIsDialogOpen(true)}>
                <Plus className="w-4 h-4 mr-2" />
                Crear Primera Tarea
              </Button>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  );
}
